﻿.skills-section {
  width: 100%; }

.skills-subsection {
  margin-bottom: 50px;
  padding: 30px 20px;
  background-color: #2b2a44;
  &__title {
    margin-bottom: 20px;
    padding-bottom: 5px;
    border-bottom: 1px solid $color-green;
    @include fz(26);
    font-weight: 300; } }

.skills-list {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  @include md {
    align-items: center; }
  .skills-item {
    width: 45%;
    margin-right: 5%;
    margin-bottom: 40px;
    &:nth-child(2n) {
      margin-right: 0; }
    &:last-child,
    &:nth-last-child(2) {
      margin-bottom: 0; }
    @include md {
      width: 23%;
      margin-right: 2.6%;
      margin-bottom: 0;
      &:nth-child(2n) {
        margin-right: 2.6%; }
      &:last-child {
        margin-right: 0; } } } }

.skills-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  &:hover {
    .skills-item {
      &__img {
        filter: grayscale(0%); }
      &__desc {
        color: #2b2a44;
        &::before {
          @include center('h'); }
        &::after {
          left: 100%; } } } }
  &__img {
    max-width: 100%;
    max-height: 100px;
    filter: grayscale(100%);
    transition: filter 0.5s;
    @include md {
      max-height: 120px; }
    &-wrapper {
      display: flex;
      width: 100%;
      height: 100px;
      margin-bottom: 20px;
      align-items: center;
      justify-content: center;
      @include md {
        height: 150px; } } }
  &__desc {
    position: relative;
    display: block;
    width: 100%;
    text-align: center;
    overflow: hidden;
    transition: color 0.4s ease;
    @include fz(18);
    &::before {
      @include pseudo;
      left: -100%;
      content: attr(desc);
      transition: all 0.2s ease;
      color: #ffffff; }
    &::after {
      @include pseudo;
      top: 0;
      left: -100%;
      width: 100%;
      height: 100%;
      background-color: $color-green;
      transition: left 0.5s ease; } } }
