﻿.contacts-list {
  &__item {
    margin-bottom: 50px; }
  &__title {
    display: block;
    margin-bottom: 10px;
    @include fz(28);
    font-weight: 300;
    @include xs {
      display: inline-block;
      margin-right: 50px; }
    @include md {
      @include fz(32); } }
  &__link {
    position: relative;
    @include fz(24);
    color: #ffffff;
    opacity: 0.7;
    transition: opacity 0.3s ease;
    &::after {
      @include pseudo;
      left: 0;
      bottom: -5px;
      width: 0;
      height: 4px;
      background-color: $color-green;
      transition: width 0.3s ease; }
    &:hover {
      opacity: 1;
      &::after {
        width: 100%; } }
    @include md {
      @include fz(28); } } }
