﻿.about-section {
  position: relative;
  width: 100%;
  height: 100%;
  &::after {
    @include pseudo;
    right: 0;
    bottom: -10px;
    width: 100%;
    height: 100%;
    background-image: url('../img/about-bg.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 200%;
    opacity: 0.05;
    @include md {
      width: 50%; } }
  .my {
    &-name {
      @include fz(38);
      font-weight: 300;
      @include xs {
        @include fz(75); }
      span {
        color: $color-green;
        font-weight: normal; } }
    &-position {
      margin-bottom: 40px;
      color: $color-green;
      @include fz(28);
      @include xs {
        @include fz(43.2); } }
    &-hobbies {
      width: 95%;
      margin-bottom: 20px;
      @include fz(22);
      font-weight: 100;
      opacity: 0.58;
      @include xs {
        width: 75%;
        @include fz(36); } } } }
